.tailor-icon-kits {
  margin-bottom: 1em;
}

.tailor-icons__actions {
  max-width: 120px;
}

.tailor-icons__actions .button,
.tailor-icons__actions .spinner {
  float: left;
}

.tailor-icons__actions::after {
  content: "";
  display: table;
  clear: both;
}

.settings-information {
  background-color: #fff;
  padding: 1em 2em;
}

@media only screen and (min-width: 641px) {
  .settings-information {
    position: absolute;
    right: 20px;
  }
}
